
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import { mapGetters } from 'vuex'
import SelectedClassifyTabs from 'base/selected-classify-tabs/selected-classify-tabs'

export default {
name: 'classify-tabs',
props: {
checkerItem: {
type: Array,
defalut: []
}
},
data () {
return {
selectedItem: []
}
},
computed: {
...mapGetters({
modifyItem: 'analysis/modifyItem'
})
},
components: {
Checker,
CheckerItem,
SelectedClassifyTabs
},
watch: {},
methods: {
onItemClick () {
this.$nextTick(() => {
this.$emit('selectedItem', this.selectedItem)
})
}
},
created () {
if (this.modifyItem.q) this.selectedItem = JSON.parse(this.modifyItem.q)
},
mounted () {}
}



